.schedule-card > div {
  height: 100%;
}

.schedule {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  max-height: 100%;
  padding: 4px;
}

.schedule div {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.schedule div.highlighted {
  background-color: white;
}

.schedule div.highlighted {
  border-top: 1px solid #f5f5f5;
}

.schedule span.handel {
  position: relative;
  top: 0;
  left: -4px;
  right:0;
  width: calc(100% + 8px) ;
  height: 2px;
  background-color: var(--main-orange);
  margin: 0 auto;
  /* padding: 0 2px; */
  z-index: 100;
  -webkit-transition: all 0.25;
  -o-transition: all 0.25;
  transition: all 0.25;
}

.schedule div span {
  text-align: left;
  padding: 6px;
  font-size: 10px;
  letter-spacing: 0.05em;
}

.schedule .timing span {
  padding: 0;
  padding-bottom: 4px;
  font-weight: bold;
  color: rgba(0, 0, 0, 0.48);
}

.schedule .shadow {
  -webkit-box-shadow: 0px 1px 2px 0px #e3e3e3;
          box-shadow: 0px 1px 2px 0px #e3e3e3;
}

.schedule .break {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  width: 100%;
}



